import { Components } from '@ionic/core';
import { defineCustomElement } from '@ionic/core/components/ion-text';
import type { Meta, StoryObj } from '@storybook/web-components';
import { html } from 'lit';
import { TP_TYPE } from '../../../../../utils/constants/type.constant';
import {
  TP_COLORS,
  TP_COLORS_MEDSOFT,
  TP_COLORS_RECURSOS,
} from '../../../../../utils/constants/color.constant';
import {
  TpColorAula,
  TpColorMaterial,
  TpColorOrange,
  TpColorProvas,
  TpColorQuestoes,
  TpColorRevalida,
} from '../../../../../utils/enums/color.enum';
import { TpIonText } from '../../utils/text.type';

defineCustomElement();

const meta: Meta<Components.IonText & TpIonText> = {
  title: 'components/Ionic/Text/Website',
  argTypes: {
    color: {
      control: { type: 'select' },
      options: [...TP_COLORS, ...TP_COLORS_MEDSOFT, ...TP_COLORS_RECURSOS],
      description: `Define a cor do componente.

**OBS**: As cores **'${TpColorAula.Aula}'**, **'${TpColorMaterial.Material}'**, **'${TpColorQuestoes.Questoes}'**, **'${TpColorRevalida.Revalida}'** e **'${TpColorProvas.Provas}'** estão disponiveis apenas no theme **MedSoft**.

**OBS**: A cor **'${TpColorOrange.Orange}'** está disponivel apenas no theme **Recursos**.`,
      table: {
        defaultValue: {
          summary: 'undefined',
        },
        type: {
          summary: [
            ...TP_COLORS,
            ...TP_COLORS_MEDSOFT,
            ...TP_COLORS_RECURSOS,
          ].join('|'),
        },
      },
    },
    ['tp-type']: {
      control: { type: 'select' },
      options: [...TP_TYPE],
      description: 'Define a estilização do texto do componente.',
      table: {
        defaultValue: { summary: 'undefined' },
        type: { summary: TP_TYPE.join('|') },
      },
    },
  },
  args: {
    'tp-type': 'p16b',
  },
};

export default meta;

type Story = StoryObj<Components.IonText & TpIonText>;

export const DefaultMarkup: Story = {
  render: () => html`
    <ion-text tp-type="h24">
      <h1>Lorem ipsum, dolor sit amet consectetur.</h1>
    </ion-text>
  `,
};
DefaultMarkup.argTypes = {
  color: { table: { disable: true } },
  'tp-type': { table: { disable: true } },
};

export const TypeMarkup: Story = {
  render: ({ ...args }) => html`
    <ion-text tp-type="${args['tp-type']}">
      <h1>Lorem ipsum, dolor sit amet consectetur.</h1>
    </ion-text>
  `,
};
TypeMarkup.argTypes = {
  color: { table: { disable: true } },
};

export const ColorsMarkup: Story = {
  render: ({ ...args }) => html`
    <ion-text color="${args.color}" tp-type="p16b">
      <p>Lorem ipsum, dolor sit amet consectetur.</p>
    </ion-text>
  `,
};
ColorsMarkup.argTypes = {
  'tp-type': { table: { disable: true } },
};
